/* Module: S */
.main_list {
    padding: 0 10px 80px 10px;
    .articles_list {
        list-style: none;
        margin: 0;
        padding: 0;
    }
    .articles_list--element {
        clear: both;
        background-color: $white;
        -webkit-box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
        -moz-box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
        box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
        height: 100px;
        overflow: hidden;
        margin-bottom: 10px;
        .img_wrapper {
            background: url({{file_dest}}/img_wrapper.png) center center/contain no-repeat;
            position: relative;
            padding-bottom: 100px;
            height: 0;
            overflow: hidden;
            margin-bottom: 0;
            width: 100px;
            float: left;
            img {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                width: 140%;
                max-width: 140%;
            }
        }
        .content_wrapper {
            position: relative;
            float: left;
            width: calc(100% - 100px);
            height: 100px;
            padding: 8px 10px;
            .article-cathegory {
                margin: 0px;
                margin-top: -4px;
                font-size: pxToRem(10);
                color: $blue;
                font-weight: $semibold;
            }
            h3 {
                color: black;
                margin: 2px 0px;
                font-size: pxToRem(18);
                font-weight: $regular;
            }
            .reading_time {
                margin: 0px;
                font-size: pxToRem(12);
                color: $gray-darkest;
                position: absolute;
                bottom: 0;
                left: 10px;
                line-height: 20px;
            }
            .timestamp {
                margin: 0px;
                font-size: pxToRem(10);
                color: $gray-darkest;
                position: absolute;
                bottom: 0;
                right: 10px;
                line-height: 20px;
            }
        }
    }
    .articles_list--element-video {
        @extend .articles_list--element;
        background-color: black;
        height: auto;
        .img_wrapper {
            padding-bottom: 146px;
            width: 100%;
            &:before {
                content: " ";
                position: absolute;
                width: 26px;
                height: 36px;
                z-index: 1;
                top: calc(50% - 18px);
                left: calc(50% - 13px);
                background: url({{file_dest}}/playbutton.svg) center center/contain no-repeat;
            }
        }
        .content_wrapper {
            color: $white;
            width: 100%;
            height: auto;
            h3 {
                color: $white;
                margin: 0 0 13px 0;
            }
            .article-cathegory {
                color: $white;
                font-weight: $regular;
                float: left;
            }
            .timestamp {
                color: $white;
                font-weight: $regular;
                position: initial;
                float: right;
                margin-top: -4px;
            }
        }
    }
}