/* Module: S */
.login {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.logo{
    background: url({{file_dest}}/icas_logo.gif) center center/contain no-repeat;
    display: block;
    width: 100%;
    height: 57px;
    margin: 0 0 204px 0 ;
}

.button {
    background-color: $button-blue;
    font-size: pxToRem(16);
    font-weight: 400;
    padding: 0.6em 2.6em;
    &:focus,
    &:hover {
        color: $white;
        text-decoration: none;
        background-color: $blue;
    }
}