/* Module: S */
header {
    width: 100%;
    display: table;
    background-color: rgba(255,255,255,0.85);
    .gray & {
        -webkit-box-shadow: 0px 1px 10px 0px rgba(0,0,0,0.2);
        -moz-box-shadow: 0px 1px 10px 0px rgba(0,0,0,0.2);
        box-shadow: 0px 1px 10px 0px rgba(0,0,0,0.2);
    }
    .toplogo_centered {
        display: table;
        font-size: pxToRem(30);
        font-weight: 600;
        height: 39px;
        text-align: center;
        margin: auto;
        padding-top: 20px;
        span {
            padding-left: 10px;
            font-size: pxToRem(14);
            display: table-cell;
            vertical-align: middle;
        }
    }
    .toplogo {
        display: table;
        font-size: pxToRem(30);
        font-weight: 600;
        height: 39px;
        text-align: center;
        margin: auto;
        padding: 8px 15px 0 0 ;
        span {
            padding-left: 10px;
            font-size: pxToRem(14);
            display: table-cell;
            vertical-align: middle;
        }
    }
    .menutrigger {
        font-size: pxToRem(20);
        padding: 12px 5px 5px 15px;
    }
}