/* Module: S */
.sw_message {
    padding: 10px 70px;
}
.sw_message--offline {
    position: relative;
    padding: 15px 0 15px 30px;
    font-size: pxToRem(10);
    font-weight: $semibold;
    line-height: pxToRem(14);
    &:before {
        position: absolute;
        top: 15px;
        left: 0px;
        display: block;
        content: " ";
        width: 25px;
        height: 25px;
        background: url({{file_dest}}/offlineicon.svg) center center/contain no-repeat;
    }

}